import React, {Component} from 'react';
import {farmList} from './config';
import FarmCard from './FarmCard'
import Countdown from "../../components/Countdown/Countdown";
import FarmReferral from "../../components/FarmReferral/FarmReferral";
import "./Farm.scss";
import {Utils, YEAR, checkTime} from "../../utils/utils";
import CopyToClipboard from "react-copy-to-clipboard";
import {utils} from 'ethers';
import {message} from "antd";
import banner from '../../assets/images/farm/banner.jpg';
import copy from '../../assets/images/farm/copy.png';
const numeral = require("numeral");
const FARM_RELEASE_DATE = 'Tue May 24 2022 13:00:00 UTC'
class Farm extends Component{
  constructor() {
    super();
    this.state = {
      tdePrice: 0,
      tvl:0
    }
  }

  componentDidMount = () => {
    this.timer = setInterval(()=>{this.checkContract()}, 1000);
  }

  componentDidUpdate = (prevProps) => {
    if(this.props.address !== prevProps.address){
      this.checkContract();
    }
  }

  componentWillUnmount = () =>{
    clearInterval(this.timer);
    clearTimeout(this.timeoutTimer);
  }

  checkContract=()=>{
    if(Utils.tronweb && Utils.tdao && Utils.tde &&  Utils.lpvalue &&  Utils.tronweb.defaultAddress.base58 !== Utils.FOUNDATION_ADDRESS){
      clearInterval(this.timer);
      this.getPoolStats().then();
    }
  }

  getPoolStats = async () => {
    clearTimeout(this.getPoolStatsTimer);
    clearTimeout(this.readPriceTimer);
    this.getMainTokenPrice().then();
    if (this.props.nativePrice === 0) {
      this.readPriceTimer = setTimeout(() => {
        this.getPoolStats();
      }, 500);
      return;
    }

    const keys = Object.keys(Utils.liquidityPools);

    for (let i = 0; i < keys.length; i++) {
      try {
        const item = keys[i];
        if (!Utils[item] || !Utils[item].token || !Utils[item].pool) {
          continue;
        }
        const pool = Utils.liquidityPools[item];
        const newState = {};
        //calc tokenPrice
        let decimals = await Utils[item].token.decimals().call({ _isConstant: true })

        if (pool.isLp) {
          const swapPoolValue = await Utils.lpvalue.getPool(Utils.ownerAddr, [Utils[item].pair.address]).call({ _isConstant: true })
          // const tokenStaked = parseInt(swapPoolValue._token[0]._hex)/1000000;
          // const trxStaked = parseInt(swapPoolValue._trx[0]._hex)/1000000;
          // const uniToken = parseInt(swapPoolValue._uniToken[0]._hex)/1000000;
          // console.log()
          // const liquidityPrice = tokenStaked * this.props.uniPrice + trxStaked * this.props.trxPrice;
          // newState[`${item}TokenPrice`] = liquidityPrice/uniToken;
          const trxBalanceAtPair = await Utils.tronweb.trx.getUnconfirmedBalance(Utils[item].pair.address);
          const lpTotalSupply = Utils.tronweb.fromSun(swapPoolValue._totalsupply)
          const tokenPrice = (trxBalanceAtPair/ Utils.unit.sun * this.props.trxPrice * 2)/lpTotalSupply
          newState[`${item}TokenPrice`] = tokenPrice;
        } else {
          const trxBalanceAtPair = await Utils.tronweb.trx.getUnconfirmedBalance(Utils[item].pair.address);
          const tokenBalanceAtPair = await Utils[item].token.balanceOf(Utils[item].pair.address).call();
          let tokenPrice = trxBalanceAtPair/ Utils.unit.sun / (parseInt(tokenBalanceAtPair._hex)/Utils.unit.ether) * this.props.trxPrice
          if (decimals !== 18) {
            tokenPrice = trxBalanceAtPair/ Utils.unit.sun / (parseInt(tokenBalanceAtPair._hex)/Math.pow(10, decimals)) * this.props.trxPrice
          }
          newState[`${item}TokenPrice`] = tokenPrice;
        }
        // console.log(item, newState[`${item}TokenPrice`])
        const totalSupplySource = (await Utils[item].pool.totalSupplySource().call({ _isConstant: true }))._hex;
        const rewardRate = parseInt((await Utils[item].pool.rewardRate().call({ _isConstant: true }))._hex);
        const balanceSourceOf = (await Utils[item].pool.balanceSourceOf(Utils.tronweb.defaultAddress.base58).call({ _isConstant: true }))._hex
        // message.info(Utils[item].token)
        const availableSourceInWallet = (await Utils[item].token.balanceOf(Utils.tronweb.defaultAddress.base58).call({ _isConstant: true }))._hex
        const rewardsEarned = parseInt((await Utils[item].pool.earned(Utils.tronweb.defaultAddress.base58).call({ _isConstant: true }))._hex);
        const sourceAllowance = parseInt((await Utils[item].token.allowance(Utils.tronweb.defaultAddress.base58, Utils[item].pool.address).call({ _isConstant: true }))._hex);
        // calc tvl
        let totalTokenStaked = utils.formatEther(totalSupplySource);

        if (decimals !== 18) {
          totalTokenStaked = Utils.tronweb.fromSun(totalSupplySource)
           // console.log(item, totalTokenStaked)
        }

        newState[`${item}TotalStaked`] = totalTokenStaked;
        const tokenTvl = parseFloat(totalTokenStaked) * parseFloat(newState[`${item}TokenPrice`]);
        newState[`${item}Tvl`] = tokenTvl;

        // calc apy
        const tokenRewardRate = rewardRate/ Utils.unit.ether;
        // console.log(item, tokenRewardRate);

        let poolApr = (tokenRewardRate * YEAR * this.state.tdePrice) / parseFloat(tokenTvl);
        if (!isNaN(poolApr) && poolApr !== Infinity) {
          newState[`${item}Apy`] = poolApr;
        }
        // console.log(item, tokenRewardRate * 86400,inviterBonusPerMillion/10000,newState[`${item}StartTime`]);
        // calc user balance
        let myLiquidityBalance = utils.formatEther(balanceSourceOf);

        if (decimals !== 18) {
          myLiquidityBalance = parseInt(balanceSourceOf) / Math.pow(10, decimals);
        }
        const myLiquidityBalanceValue = parseFloat(myLiquidityBalance) * parseFloat(newState[`${item}TokenPrice`]);
        newState[`${item}LiquidityBalance`] = myLiquidityBalance;
        newState[`${item}LiquidityBalanceValue`] = myLiquidityBalanceValue;
        let sourceInWallet = utils.formatEther(availableSourceInWallet)
        if(decimals !== 18){
          sourceInWallet = availableSourceInWallet/ Math.pow(10, decimals);
        }
        newState[`${item}SourceInWallet`] = sourceInWallet < 0.0000009 ? 0 : sourceInWallet

        newState[`${item}Earned`] = rewardsEarned / Utils.unit.ether;

        newState[`${item}Allowance`] = decimals !== 18 ? sourceAllowance / Math.pow(10, decimals) : sourceAllowance/Utils.unit.ether
        newState[`${item}SourceDecimal`] = decimals;
        this.setState(newState,()=>{
          this.updateTvl();
        });
      } catch (e) {
        console.log(keys[i], e);
      }
    }



    this.getPoolStatsTimer = setTimeout(() => {
      this.getPoolStats();
    }, 10000);
  };

  getMainTokenPrice = async () => {
    try {
        const trxBalanceAtPair = await Utils.tronweb.trx.getUnconfirmedBalance(Utils.addresses.tdePair);
        const tdeBalanceAtPair = await Utils.tde.balanceOf(Utils.addresses.tdePair).call();
        const tdePrice = trxBalanceAtPair/ Utils.unit.sun / (parseInt(tdeBalanceAtPair._hex)/Utils.unit.ether) * this.props.trxPrice
        this.setState({
          tdePrice:tdePrice
        })

    } catch (e) {
      console.log(e);
    }
  };

  updateTvl = () => {
    let tvl = 0;

    Object.keys(Utils.liquidityPools).map(async (item, index) => {
      if (parseFloat(this.state[`${item}Tvl`]) > 0) {
        tvl += parseFloat(this.state[`${item}Tvl`]);
      }
    });

    this.setState({
      tvl: tvl,
    });
  };
  render(){

    return(
      <div className="farm" align="left">

          <div className="banner" style={{backgroundImage: `url(${banner})})`}}>
            <div className={"mainContent"}>
              <div>
                <h2>BeefDAO Farm</h2>
                <h1>The Revolutionary Yield Farming System on Binance Smart Chain</h1>
              </div>

              {
                checkTime(FARM_RELEASE_DATE) ?
                  <div className={'countdownWrap'}>
                    <h2>TVL: </h2>
                    <div>
                      <strong>
                        {numeral(this.state.tvl).format('$0,0.[00]')}
                      </strong>
                    </div>
                  </div>:
                  <div className={'countdownWrap'}>
                <h2>Mining Starts In: </h2><Countdown date={FARM_RELEASE_DATE}/>
              </div>
              }

            </div>
            <a href={`https://pancakeswap.finance/swap?outputCurrency=${Utils.addresses.tokens.rbd}`} target="_blank" rel="noopener noreferrer">
              <div className={"buyToken"}>
                Buy RBD
              </div>
            </a>

        </div>


        <div className={"farmWrap"}>
          <div className={"farmInner"}>
            {
              Object.keys(farmList).map((item) => {
                return <FarmCard id={item} info={farmList[item]} key={item} data={this.state}/>
              })
            }
          </div>
        </div>

        <FarmReferral
          address={Utils.tronweb && Utils.tronweb.defaultAddress.base58 && Utils.tronweb.defaultAddress.base58 !== Utils.FOUNDATION_ADDRESS ? Utils.tronweb.defaultAddress.base58 : null}
        />




      </div>
    )
  }
}

export default Farm;
